'use client';

import React, { useState, useEffect, useRef } from 'react';
import { DndContext, useDraggable, useDroppable, DragOverlay, MouseSensor, TouchSensor, useSensor, useSensors, DragEndEvent, DragStartEvent } from '@dnd-kit/core';
import { restrictToParentElement } from '@dnd-kit/modifiers';
import { Save, Type, Image as ImageIcon, Minus, Box, ArrowLeft } from 'lucide-react';
import Link from 'next/link';
import { v4 as uuidv4 } from 'uuid';

// Types
type ElementType = 'text' | 'image' | 'box' | 'line';

export interface EditorElement {
  id: string;
  type: ElementType;
  x: number;
  y: number;
  w: number;
  h: number;
  content?: string;
  style?: React.CSSProperties;
}

// Components (Will refactor later if too big)
function SidebarItem({ type, icon: Icon, label }: { type: ElementType, icon: any, label: string }) {
  const { attributes, listeners, setNodeRef } = useDraggable({
    id: `sidebar-${type}`,
    data: { type, isSidebar: true },
  });

  return (
    <div ref={setNodeRef} {...listeners} {...attributes} className="p-4 bg-white mb-2 border rounded cursor-grab hover:bg-gray-50 flex items-center gap-2">
      <Icon size={20} />
      <span>{label}</span>
    </div>
  );
}

function DraggableElement({ element, isSelected, onSelect }: { element: EditorElement, isSelected: boolean, onSelect: (id: string) => void }) {
  const { attributes, listeners, setNodeRef, transform } = useDraggable({
    id: element.id,
    data: { ...element, isSidebar: false },
  });

  const style: React.CSSProperties = {
    position: 'absolute',
    top: element.y,
    left: element.x,
    width: element.w,
    height: element.h,
    transform: transform ? `translate3d(${transform.x}px, ${transform.y}px, 0)` : undefined,
    border: isSelected ? '2px solid #2563eb' : '1px dashed transparent',
    ...element.style,
  };

  // Apply content specific rendering
  const renderContent = () => {
    switch (element.type) {
      case 'text':
        return <div className="w-full h-full overflow-hidden">{element.content || 'Texte'}</div>;
      case 'box':
        return <div className="w-full h-full bg-gray-200 border border-gray-400"></div>;
      case 'line':
        return <div className="w-full h-[2px] bg-black mt-[50%]"></div>;
      default:
        return <div>Element</div>;
    }
  };

  return (
    <div 
      ref={setNodeRef} 
      style={style} 
      {...listeners} 
      {...attributes}
      onClick={(e) => { e.stopPropagation(); onSelect(element.id); }}
      className="hover:border-blue-300 cursor-move group"
    >
      {renderContent()}
      {isSelected && <div className="absolute -top-6 left-0 bg-blue-600 text-white text-xs px-1 rounded">x:{element.x} y:{element.y}</div>}
    </div>
  );
}

export default function BuilderClient({ template }: { template: any }) {
  const [elements, setElements] = useState<EditorElement[]>(() => {
    try { return JSON.parse(template.content); } catch { return []; }
  });
  const [selectedId, setSelectedId] = useState<string | null>(null);
  const [activeDragId, setActiveDragId] = useState<string | null>(null);
  const [activeDragData, setActiveDragData] = useState<any>(null);
  const [saving, setSaving] = useState(false);

  // Canvas Ref for bounds
  const canvasRef = useRef<HTMLDivElement>(null);

  const sensors = useSensors(
    useSensor(MouseSensor, { activationConstraint: { distance: 5 } }),
    useSensor(TouchSensor)
  );

  const handleDragStart = (event: DragStartEvent) => {
    setActiveDragId(event.active.id as string);
    setActiveDragData(event.active.data.current);
  };

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over, delta } = event;
    setActiveDragId(null);
    setActiveDragData(null);

    if (!over) return;

    const isSidebar = active.data.current?.isSidebar;

    if (isSidebar) {
      // Add new element
      const type = active.data.current?.type as ElementType;
      // Calculate position relative to canvas
      // This is tricky without droppable rect, simplifying: drop at mouse pos approx or center
      // Better: use delta + initial mouse pos? 
      // For now: drop at fixed offset or try to map coordinates.
      // Let's just add it at a default position if dropped on canvas, 
      // OR if we want precise drop, we need to calculate offsets.
      // Simplification: Add to (50, 50) + delta if possible, or just (50, 50)
      
      const newElement: EditorElement = {
        id: uuidv4(),
        type,
        x: 50, // Default, ideally calculate based on drop position
        y: 50,
        w: type === 'line' ? 200 : 150,
        h: type === 'line' ? 2 : 50,
        content: type === 'text' ? 'Nouveau texte' : undefined,
        style: { fontSize: '16px', color: '#000000' }
      };
      setElements([...elements, newElement]);
      setSelectedId(newElement.id);
    } else {
      // Move existing element
      const id = active.id as string;
      setElements(elements.map(el => {
        if (el.id === id) {
          return {
            ...el,
            x: el.x + delta.x,
            y: el.y + delta.y
          };
        }
        return el;
      }));
    }
  };

  const saveTemplate = async () => {
    setSaving(true);
    await fetch(`/api/templates/${template.id}`, {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ content: JSON.stringify(elements) }),
    });
    setSaving(false);
  };

  const updateSelected = (key: keyof EditorElement | 'style', value: any) => {
    if (!selectedId) return;
    setElements(elements.map(el => {
      if (el.id === selectedId) {
        if (key === 'style') {
            return { ...el, style: { ...el.style, ...value } };
        }
        return { ...el, [key]: value };
      }
      return el;
    }));
  };

  const selectedElement = elements.find(el => el.id === selectedId);

  return (
    <DndContext sensors={sensors} onDragStart={handleDragStart} onDragEnd={handleDragEnd}>
      <div className="h-[calc(100vh-64px)] flex flex-col">
        {/* Toolbar */}
        <div className="h-14 bg-white border-b flex items-center justify-between px-4">
           <div className="flex items-center gap-4">
             <Link href="/dashboard" className="text-gray-500 hover:text-gray-700"><ArrowLeft size={20} /></Link>
             <h1 className="font-semibold">{template.name}</h1>
           </div>
           <button 
             onClick={saveTemplate}
             className="bg-blue-600 text-white px-4 py-2 rounded flex items-center gap-2 hover:bg-blue-700"
             disabled={saving}
            >
             <Save size={18} /> {saving ? 'Sauvegarde...' : 'Sauvegarder'}
           </button>
        </div>

        <div className="flex-1 flex overflow-hidden">
          {/* Sidebar */}
          <div className="w-64 bg-gray-100 border-r p-4 overflow-y-auto">
            <h3 className="font-bold mb-4 text-gray-700">Éléments</h3>
            <SidebarItem type="text" icon={Type} label="Texte" />
            <SidebarItem type="box" icon={Box} label="Boîte" />
            <SidebarItem type="line" icon={Minus} label="Ligne" />
          </div>

          {/* Canvas Area */}
          <div className="flex-1 bg-gray-200 p-8 overflow-auto flex justify-center">
             {/* A4 Canvas: 210mm x 297mm. Approx 794px x 1123px at 96dpi */}
             <div 
               ref={canvasRef}
               id="invoice-canvas"
               onClick={() => setSelectedId(null)}
               className="bg-white shadow-xl relative"
               style={{ width: '794px', height: '1123px', minWidth: '794px', minHeight: '1123px' }}
             >
               {useDroppable({ id: 'canvas' }).setNodeRef}
               {elements.map(el => (
                 <DraggableElement 
                   key={el.id} 
                   element={el} 
                   isSelected={selectedId === el.id} 
                   onSelect={setSelectedId} 
                  />
               ))}
             </div>
          </div>

          {/* Properties Panel */}
          <div className="w-72 bg-white border-l p-4 overflow-y-auto">
            <h3 className="font-bold mb-4 text-gray-700">Propriétés</h3>
            {selectedElement ? (
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium">Type</label>
                  <div className="text-gray-500 capitalize">{selectedElement.type}</div>
                </div>
                
                <div className="grid grid-cols-2 gap-2">
                   <div>
                    <label className="block text-sm font-medium">X</label>
                    <input type="number" value={selectedElement.x} onChange={(e) => updateSelected('x', parseInt(e.target.value))} className="w-full border rounded px-2 py-1" />
                   </div>
                   <div>
                    <label className="block text-sm font-medium">Y</label>
                    <input type="number" value={selectedElement.y} onChange={(e) => updateSelected('y', parseInt(e.target.value))} className="w-full border rounded px-2 py-1" />
                   </div>
                   <div>
                    <label className="block text-sm font-medium">L</label>
                    <input type="number" value={selectedElement.w} onChange={(e) => updateSelected('w', parseInt(e.target.value))} className="w-full border rounded px-2 py-1" />
                   </div>
                   <div>
                    <label className="block text-sm font-medium">H</label>
                    <input type="number" value={selectedElement.h} onChange={(e) => updateSelected('h', parseInt(e.target.value))} className="w-full border rounded px-2 py-1" />
                   </div>
                </div>

                {selectedElement.type === 'text' && (
                  <>
                    <div>
                      <label className="block text-sm font-medium">Contenu</label>
                      <textarea 
                        value={selectedElement.content} 
                        onChange={(e) => updateSelected('content', e.target.value)}
                        className="w-full border rounded px-2 py-1"
                        rows={3}
                      />
                    </div>
                    <div>
                       <label className="block text-sm font-medium">Taille police</label>
                       <input 
                         type="text" 
                         value={selectedElement.style?.fontSize || ''} 
                         onChange={(e) => updateSelected('style', { fontSize: e.target.value })} 
                         className="w-full border rounded px-2 py-1"
                       />
                    </div>
                  </>
                )}
                 <button 
                   onClick={() => {
                     setElements(elements.filter(e => e.id !== selectedId));
                     setSelectedId(null);
                   }}
                   className="w-full bg-red-100 text-red-600 py-2 rounded mt-4 hover:bg-red-200"
                 >
                   Supprimer
                 </button>
              </div>
            ) : (
              <div className="text-gray-400 text-sm">Sélectionnez un élément pour modifier ses propriétés.</div>
            )}
          </div>
        </div>
      </div>
      <DragOverlay>
        {activeDragId ? (
            <div className="bg-blue-500/50 p-4 rounded text-white">
               {activeDragData?.type}
            </div>
        ) : null}
      </DragOverlay>
    </DndContext>
  );
}

